import * as Tone from 'tone'
import { CellularAutomata1D } from "../cellular-automata";
import { Chord } from "@tonaljs/tonal";

const NOTE_DURATION = 40;

export class CellularAutomata1DPlayer {
    private note = "";
    private lastNote = "";
    private voice = "";
    private lastVoice = "";
    private lastChord = [""]
    private chords = [
        ["C2", "E2", "G2"],
        //["D2", "F2", "A2"],
        ["E2", "G2", "B2"],
        ["F2", "A2", "C3"],
        //["G2", "B2", "D3"],
        ["A2", "C3", "E3"],
        //["B2", "D3", "F3"],
        ["C2", "E2", "G2", "B2"],
        //["D2", "F2", "A2", "C3"],
        ["E2", "G2", "B2", "D3"],
        ["F2", "A2", "C3", "E3"],
        //["G2", "B2", "D3", "F3"],
        ["A2", "C3", "E3", "G3"],
        //["B2", "D3", "F3", "A3"],
        // --- +1 Octave --- //
        ["C3", "E3", "G3"],
        //["D3", "F3", "A3"],
        ["E3", "G3", "B3"],
        ["F3", "A3", "C4"],
        //["G3", "B3", "D4"],
        ["A3", "C4", "E4"],
        //["B3", "D4", "F4"],
        ["C3", "E3", "G3", "B3"],
        //["D3", "F3", "A3", "C4"],
        ["E3", "G3", "B3", "D4"],
        ["F3", "A3", "C4", "E4"],
        //["G3", "B3", "D4", "F4"],
        ["A3", "C4", "E4", "G4"],
        //["B3", "D4", "F4", "A4"],
    ];
    private instrument: Tone.Sampler;
    private automata: CellularAutomata1D;
    private offset = 0;
    private chordsDistance = 8;
    private voicesDistance = 4;
    private noteDistance = 1;
    private step = 0;

    constructor(instrument: Tone.Sampler, automata: CellularAutomata1D, offset: number) {
        this.instrument = instrument;
        this.automata = automata;
        this.offset = offset;
    }

    play() {
        this.playChord();
        this.playNote();
        this.playVoice();

        this.step++;
    }

    private playChord() {
        let targetIndex = this.leeDistance(0) % (this.chords.length * 2);
        let actualIndex = targetIndex >= this.chords.length ? this.chords.length - (targetIndex % this.chords.length) - 1 : targetIndex;
        let chord = this.chords[actualIndex % this.chords.length];
        if (this.step % this.chordsDistance == 0) {
            let midiNotes = chord.map((note) => Tone.Frequency(note).transpose(this.offset).toFrequency())
            this.instrument.triggerAttackRelease(midiNotes, NOTE_DURATION * 4, undefined, 0.075);
            this.lastChord = chord
        }
    }

    private playNote() {
        let targetIndex = this.leeDistance(1) % (this.lastChord.length * 2);
        let actualIndex = targetIndex >= this.lastChord.length ? this.lastChord.length - (targetIndex % this.lastChord.length) - 1 : targetIndex;
        this.note = this.lastChord[actualIndex % this.lastChord.length];

        if (this.step % this.noteDistance == 0 && this.note != this.lastNote) {
            let midiNote = Tone.Frequency(this.note).transpose(this.offset + 12).toFrequency();
            this.instrument.triggerAttackRelease(midiNote, NOTE_DURATION, undefined, 0.1);
        }

        this.lastNote = this.note;
    }

    private playVoice() {
        let targetIndex = this.leeDistance(2) % (this.lastChord.length * 2);
        let actualIndex = targetIndex >= this.lastChord.length ? this.lastChord.length - (targetIndex % this.lastChord.length) - 1 : targetIndex;
        this.voice = this.lastChord[actualIndex % this.lastChord.length];

        if (this.step % this.voicesDistance == 0 && this.automata.states == 3 && this.voice != this.lastVoice) {
            let midiNote = Tone.Frequency(this.voice).transpose(this.offset + 24).toFrequency();
            this.instrument.triggerAttackRelease(midiNote, NOTE_DURATION * 2, undefined, 0.2);
        }

        this.lastVoice = this.voice;
    }

    private leeDistance(state: number) {
        return this.automata.state.reduce((acc, _, index) => {
            let euclideanDistance = Math.abs(this.automata.state[index] - this.automata.previousState[index])
            let leeDistance = Math.min(euclideanDistance, this.automata.states - euclideanDistance)
            return this.automata.state[index] != state ? acc : acc + leeDistance;
        }, 0);
    }

    stop() {
        this.instrument.releaseAll();
        this.instrument.dispose();
        this.note = "";
        this.lastNote = "";
        this.step = 0;
    }

    static Builder = class {
        private automata?: CellularAutomata1D
        private offset = 0;

        withAutomata(automata: CellularAutomata1D) {
            this.automata = automata;
            return this;
        }

        withOffset(offset: number) {
            this.offset = offset;
            return this;
        }

        async build() {
            if (this.automata === null) {
                throw new Error("Must pass a cellular automata upon building");
            }
            const instrument = new Tone.Sampler({
                urls: {
                    "C1": "C1.mp3",
                    "D#1": "Ds1.mp3",
                    "F#1": "Fs1.mp3",
                    "A1": "A1.mp3",
                    "C2": "C2.mp3",
                    "D#2": "Ds2.mp3",
                    "F#2": "Fs2.mp3",
                    "A2": "A2.mp3",
                    "C3": "C3.mp3",
                    "D#3": "Ds3.mp3",
                    "F#3": "Fs3.mp3",
                    "A3": "A3.mp3",
                    "C4": "C4.mp3",
                    "D#4": "Ds4.mp3",
                    "F#4": "Fs4.mp3",
                    "A4": "A4.mp3",
                    "C5": "C5.mp3",
                    "D#5": "Ds5.mp3",
                    "F#5": "Fs5.mp3",
                    "A5": "A5.mp3",
                },
                baseUrl: "https://tonejs.github.io/audio/salamander/",
            }).toDestination();

            await Tone.loaded();

            return new CellularAutomata1DPlayer(instrument, this.automata!, this.offset);
        }
    }
}